import React from 'react';
import {useParams} from "react-router-dom"
import InpatientReportsByOrganizationsFgmsContainer from "../containers/InpatientReportsByOrganizationsFgmsContainer";

const InpatientReportsByOrganizationsFgmsPage = () => {
    const {year, month,group} = useParams()
    return (
        <>
            <InpatientReportsByOrganizationsFgmsContainer year={year} month={month} group={group}/>
        </>
    );
};

export default InpatientReportsByOrganizationsFgmsPage;